<template>
    <div class="register">
        <div class="register-box">
            <div class="register-infor">
                <div class="tel">
                    <Icon type="md-phone-portrait" />
                    <input class="username" type="number" v-model="regtel" placeholder="请输入您的手机号码">
                </div>
                <div class="name">
                    <Icon type="md-contact" />
                    <input class="nickname" type="text" v-model="regname" placeholder="请输入您的昵称">
                </div>
                <div class="pas">
                    <Icon type="ios-unlock-outline" />
                    <input class="password" type="text" v-model="regpas" placeholder="请输入您的密码">
                </div>
                <div class="pas">
                    <Icon type="md-lock" />
                    <input class="secpassword" type="text" v-model="regpass" placeholder="请确认您的密码">
                </div>
                <div class="btn" @click="regValidate()"><button type="button"></button>确认注册</div>
            </div>
        </div>
    </div>
</template>


<script>
    export default {
        title:"Register",
        data(){
            return {
                regtel:'',
                regname:'',
                regpas:'',
                regpass:''
            }
        },
        methods:{
            regValidate(){
                // 判断输入数据的长度是否满足要求
                if(!this.regtel || !this.regname || !this.regpas || !this.regpass){
                    alert("请输入正确的信息");
                }else if((this.regtel || this.regname || this.regpas || this.regpass) 
                    && (this.regpas == this.regpass) 
                    && (this.regpas.length == 6 && this.regtel.length == 11)){
                        // 注册成功将数据存在本地
                        let userInfo = {
                            tel:this.regtel,
                            name:this.regname,
                            pas:this.regpas,
                            pass:this.regpass
                        };
                        let userArr = [];
                        if(localStorage.userInfo){
                            userArr = JSON.parse(localStorage.userInfo);
                        };
                        userArr.push(userInfo);
                        localStorage.userInfo = JSON.stringify(userArr);
                        localStorage.isLogin = true;
                        alert('注册成功!');
                        alert("请登录......");
                        setTimeout(()=>{
                            this.$router.push("/mine/login");
                        },1000);
                }else{
                    alert("格式错误(手机号码是11位数字，密码不能超过6位)!");
                }
            }   
        }
    }
    
</script>


<style scoped>
.register-box {
    margin-top:20px;
    width:100%;
}
.register-infor {
    width:100%;
}
input {
    outline:none;
    border:none;
}
.tel,.pas,.name {
    margin-top:10px;
    height:60px;
    border-bottom:1px solid #ddd;
}
.tel input,
.name input,
.pas input {
    width:85%;
    height:100%;
    font-size:17px;
    color:#000;
    padding-left:10px;
    vertical-align:middle;
}
.ivu-icon-md-phone-portrait,
.ivu-icon-md-contact,
.ivu-icon-md-lock {
    font-size:26px;
    color:#888;
}
.ivu-icon-ios-unlock-outline {
    font-size:26px;
}
.btn {
    margin-top:40px;
    height:48px;
    width:100%;
    line-height:48px;
    background:#0c9e0c;
    color:#fff;
    font-size:18px;
    letter-spacing:15px;
    text-align:center;
    border-radius:5px;
}
</style>


