<template>
    <div class="shopping content">
        <div class="null-shopping" v-show="isnull">
            <p>亲，还没有加入商品哦!</p>
            <p>去书城逛逛吧~</p>
            <div class="icon-img"></div>
        </div>
        <div class="edit" @click="editBtn()" :class="{'hidden':isEditshow}" >编辑</div>
        <div class="check-state" v-show="isEditshow">
            <span class="cancel" @click="cancelBtn()">取消</span>
        </div>
        <div class="shopping-box">
            <div class="shopping-buy" v-for="(item, index) in shoppingArr" :key="index" :style="styleHeight">
                <p class="delete" @click="deleteBtn($event,index)" v-show="isEditshow" v-if="box">删除</p>
                <div class="checked" v-show="isEditshow">
                    <Icon class="no" @click="checkBtn(index)" v-show="isEditshow" type="ios-radio-button-off" />
                    <Icon class="yes"  @click="blackBtn(index)" type="md-checkmark-circle" />
                </div>
                <div class="shopping-left" :style="styleChange"><img :src="item.cover" alt="" :style="imgHeight"></div>
                <div class="shopping-right" :style="styleObj">
                    <p class="book-name">{{item.name}}</p>
                    <p class="book-author">{{item.author}}</p>
                    <div class="shopping-price">
                        <p class="book-price">￥{{(item.price * item.num).toFixed(2)}}</p>
                        <div class="shopping-num" v-show="!isEditshow">
                            <button class="redu" @click="reduBtn(item,index)">-</button>
                            <input class="number" type="number" :value="item.num"></input>
                            <button class="add" @click="addBtn(item,index)">+</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="shopping-btn" v-show="isHidden">
            <div class="btn-money">
                <span>总价：￥</span>
                <span class="money">{{changeMoney}}</span>
            </div>
            <div class="btn-num">
                <span>结算：</span>
                <span class="count">{{this.shoppingArr.length}}</span>
            </div>
        </div>
        <div class="mask" v-show="ok">
            <div class="mask-con">
                <div class="con-top">{{alertText}}</div>
                <div class="con-bottom">
                    <span @click="closeBtn">确认</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'Shopping',
        data(){
            return {
                shoppingArr:{},
                styleChange:{
                    width:""
                },
                styleObj:{
                    width:''
                },
                styleHeight:{
                    height:''
                },
                imgHeight:{
                    height:''
                },
                isEditshow:false,
                isHidden:true,
                money:'',
                count:'',
                isnull:true,
                alertText:"已删除！",
                ok:false,
                box:true
            }
        },
        mounted(){
            // 将加入购物车的书籍存入新的对象，展示在页面
            let books = localStorage.bookData;
            let booksObj = [];
            if(books){
                booksObj = JSON.parse(localStorage.bookData);
                this.isnull = !this.isnull;
            };
            localStorage.bookData = JSON.stringify(booksObj);
            this.shoppingArr = booksObj;
            // 购物车没有数据
            if(!this.shoppingArr){
                this.isnull = !this.isnull;
            }
        },
        computed:{
            // 购物车价钱更新
            changeMoney(){
                let payArr = this.shoppingArr;
                this.money = 0;
                for(let i = 0,len = payArr.length;i < len;i++){
                    this.money += parseFloat((payArr[i].price) * (payArr[i].num) );
                }
                return this.money.toFixed(2);
            }
        },
        methods:{
            // 点击编辑按钮
            editBtn(){
                this.isEditshow = !this.isEditshow;
                this.isHidden = !this.isHidden;
                this.styleChange.width = '30%';
                this.styleObj.width = '50%';
                this.styleHeight.height = '180px';
                this.imgHeight.height = "90%";
            },
            // 点击取消按钮
            cancelBtn(){
                this.isEditshow = !this.isEditshow;
                this.isHidden = !this.isHidden;
                this.styleChange.width = '30%';
                this.styleObj.width = '65%';
                this.styleHeight.height = '140px';
                this.imgHeight.height = "100%";
            },
            // 点击小圆圈，选中书籍,添加被选中状态
            checkBtn(index) {
                $(".yes").eq(index).addClass("show");
            },
            blackBtn(index){
                $(".yes").eq(index).removeClass("show");
            },
            // 弹框点击
            closeBtn(){
                this.ok = false;
            },
            // 点击删除选中
            deleteBtn($event,index){
                $(".delete").eq(index).addClass("delColor");
                if($('.yes').eq(index).hasClass("show") && $(".delete").eq(index).hasClass("delColor")){
                    this.ok = true;
                    // 移除页面数据
                    let removeItem = [];
                    removeItem = this.shoppingArr.splice(index,1);
                    // 删除本地数据
                    let book = localStorage.bookData;
                    let arr = [];
                    if(book){
                        arr = JSON.parse(localStorage.bookData);
                        arr.splice(index,1);
                    }
                    localStorage.bookData = JSON.stringify(arr);
                };
                $('.yes').eq(index).removeClass("show");
                
            },
            // 点击+号
            addBtn(item,index){
                // 加号的数量
                item.num += 1;
                if(item.num > 5){
                    item.num = 5;
                }
                // 点击加号,本地数量的修改
                let bookArr = localStorage.bookData;
                let booksNumber = [];
                if(bookArr){
                    booksNumber = JSON.parse(bookArr);
                    for(let i = 0,len = booksNumber.length;i < len;i++){
                        if(booksNumber[i].bookNum == item.bookNum){
                            booksNumber[i].num = item.num;
                            booksNumber[i].total = (booksNumber[i].num * booksNumber[i].price).toFixed(2);
                        }
                    }
                    localStorage.bookData = JSON.stringify(booksNumber);
                }
            },
            // 点击减号,价钱、数量与总价的更新
            reduBtn(item,index){
                // 数量更新
                if(item.num === 1){
                    return;
                }
                item.num = item.num - 1;
                // 点击减号,让本地数据一并删除
                let bookArr = localStorage.bookData;
                let booksNumber = [];
                if(bookArr){
                    booksNumber = JSON.parse(bookArr);
                    for(let i = 0,len = booksNumber.length;i < len;i++){
                        if(booksNumber[i].bookNum == item.bookNum){
                            booksNumber[i].num = item.num;
                            booksNumber[i].total = (booksNumber[i].num * booksNumber[i].price).toFixed(2);
                        }
                    }
                    localStorage.bookData = JSON.stringify(booksNumber);
                }
            }
        }
    }
</script>

<style scoped>
    .shopping {
        width:100%;
        height:calc(100% - 64px -51px);
        box-sizing:border-box;
        background:#eeeeee;
        position:relative;
    }
    .null-shopping {
        width:100%;
        height:100%;
        background:#fff;
        position:absolute;
        top:0;
        left:0;
    }
    .null-shopping p {
        margin:20px 0;
        font-size:18px;
        color:#000;
        letter-spacing:2px;
        color:#d74545;
    }
   .checked {
       width:calc(100% - 87%);
       height:90%;
       display:inline-block;
       line-height:119px;
       position:relative;
   }
   .ivu-icon-ios-radio-button-off {
       font-size:22px;
       color:#666;
       display:inline-block;
   }
   .ivu-icon-md-checkmark-circle {
       font-size:22px;
       display:inline-block;
       position:absolute;
       top:42.5%;
       left:50%;
       transform:translate(-50%,-50%);
       display:none;
   }
    .edit {
        padding:0 12px;
        margin:8px 0;
        text-align:right;
        font-size:18px;
        color:#0f78cc;
    }
    .hidden{
        display:none;
    }
    .show{
        display:block;
    }
    /**点击编辑时的状态 */
    .check-state {
        padding:0 12px;
        margin:8px 0;
        text-align:right;
        font-size:18px;
    }
    .cancel {
        color:#0f78cc;
    }
    .shopping-box {
        padding:0 12px;
        width:100%;
        box-sizing:border-box;
    }
    .shopping-buy {
        padding-top:10px;
        padding-right:10px;
        padding-bottom:10px;
        padding-left:5px;
        width:100%;
        height:150px;
        border-bottom:1px solid #ccc;
        background:#fff;
    }
    .delete {
        color:red;
        font-size:14px;
        text-align:right;
    }
    .delColor {
        color:red;
    }
    .shopping-left {
        margin-right:10px;
        width:30%;
        height:97%;
        display:inline-block;
        vertical-align:top;
    }
    .shopping-left img{
        width:100%;
        height:100%;
    }
    .shopping-right {
        padding-top:8px;
        width:65%;
        height:90%;
        display:inline-block;
        vertical-align:top;
        text-align:left;
    }
    .shopping-right > p {
        display:inline-block;
        width:100%;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    .book-name {
        font-size:16px;
        color:#0f78cc;
    }
    .book-author {
        font-size:14px;
        color:#666;
    }
    .shopping-price {
        margin-top:12px;
        width:100%;
        height:35px;
        font-size:0;
        display:flex;
        justify-content:space-between;
        align-items:center;
    }
    .shopping-price > p {
        display:inline-block;
        font-size:18px;
        color:red;
        line-height:35px;
    }
    .shopping-num {
        display:inline-block;
    }
    .number {
        outline:none;
        border:1px solid #aaa;
        width:40px;
        height:35px;
        font-size:16px;
        vertical-align:middle;
        border-right:none;
        border-left:none;
        text-align:center;
    }
    .redu,.add {
        width:30px;
        height:35px;
        display:inline-block;
        font-size:16px;
        vertical-align:middle;
        background:#fff;
        outline:none;
        border:1px solid #aaa;
    }
    .redu {
        border-top-left-radius:4px;
        border-bottom-left-radius:4px;
    }
    .add {
        border-top-right-radius:4px;
        border-bottom-right-radius:4px;
    }

    .shopping-btn {
        margin-top:40px;
        width:100%;
        height:50px;  
        display:flex;
        justify-content:space-between;
        border-top:1px solid #ccc;
        background:#fff;
    }
    .btn-num,.btn-money {
        display:inline-block;
        vertical-align:top;
        line-height:50px;
        font-size:18px;
    }
    .btn-money {
        width:60%;
        color:black;
    }
    .btn-num {
        width:30%;
        background:#c0190d; 
        color:#fff;
    }
    .money {
        color:red;
    }
    /**弹框 */
    .mask {
        width:100%;
        height:100%;
        background:rgba(0,0,0,0.3);
        position:fixed;
        top:0;
        left:0;
    }
    .mask-con {
        width:240px;
        height:130px;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        background:#fff;
        border-radius:8px;
    }
    .mask-con .con-top {
        width:100%;
        height:60%;
        text-align:center;
        line-height:78px;   
        font-size:20px;
        color:black;
        letter-spacing:2px;
    }
    .mask-con .con-bottom {
        width:100%;
        height:calc(100% - 60%);
        color:#fff;
        font-size:0;
        box-sizing:border-box;
    }
    .mask-con .con-bottom span {
        box-sizing:border-box;
        width:100%;
        line-height:52px;
        text-align:center;
        display:inline-block;
        background:cornflowerblue;
        font-size:16px;  
        cursor:pointer;
        
    }
    .mask-con .con-bottom span {
       border-bottom-left-radius:8px;
       border-bottom-right-radius:8px;
    }
</style>


