<template>
  <div id="app">
  <div class="head"><p>{{this.$store.state.text}}</p></div>
    <router-view />

    <ul class="footList">
      <router-link :to="urls.home">
        <li class="listItem">
          <Icon type="ios-home" />
          <span>首页</span>
        </li>
      </router-link>
      <router-link :to="urls.kinds">
        <li class="listItem">
          <Icon type="md-share" />
          <span>分类</span>
        </li>
      </router-link>
      <router-link :to="urls.shopping">
        <li class="listItem shopping-cart">
          <Icon type="ios-cart" />
          <span>购物</span>
          <div class="cart-num">{{val}}</div>
        </li>
      </router-link>
      <router-link :to="urls.mine">
        <li class="listItem">
          <Icon type="ios-contact" />
          <span>我的</span>
        </li>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      urls:{
        home:'/',
        kinds:'/kinds',
        shopping:'/shopping',
        mine:'/mine'
      },
      links:[
        {text:'home',path:'/'},
        {text:'kinds',path:'/kinds'},
        {text:'shopping',path:'/shopping'},
        {text:'mine',path:'/mine'}
      ],
      val:''
    }
  },
  mounted(){
     setInterval((val)=>{
          if(localStorage.bookData){
            let cartNum = JSON.parse(localStorage.bookData);
            this.val = cartNum.length;}
      },1000);
  }
}
</script>

<style>
* {
  margin:0;
  padding:0;
  box-sizing:border-box;
}
html,body {
  width:100%;
  height:100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height:100%;
}
.head {
  width:100%;
  height:64px;
  background:#444;
  text-align:center;
  line-height:64px;
  color:#fff;
  font-size:18px;
}
.content {
  width:100%;
  height:calc(100% - 52px - 64px);
  overflow-y:auto;
  overflow-x:hidden;
}
.footList {
  width:100%;
  height:52px;
  border-top:1px solid #ccc;
}
.listItem {
  padding:4px 0 0;
  height:100%;
  width:24%;
  display:inline-block;
}
.ivu-icon-ios-home,
.ivu-icon-md-share,
.ivu-icon-ios-cart,
.ivu-icon-ios-contact {
  font-size:24px;
  color:#333;
}
.listItem span {
  color:#333;
  text-decoration:none;
  font-size:14px;
  display:block;
  margin-top:2px;
}
.shopping-cart {
  position:relative;
}
.cart-num {
  width:15px;
  height:15px;
  border-radius:50%;
  position:absolute;
  right:26px;
  top:3px;
  background:red;
  color:#fff;
  line-height:14px;
}


</style>



