import Vue from 'vue'
import Router from 'vue-router'

import Home from '../pages/home'
import Kinds from '../pages/kinds'
import Shopping from '../pages/shopping'
import Mine from '../pages/mine'
import Login from '../pages/login'
import Register from '../pages/register'
import Personal from '../pages/personal'
import Details from '../pages/details'
import Buybook from "../pages/buybook"
import Material from "../pages/material"
import Adress from "../pages/adress"
import Payment from "../pages/payment"
import store from '../store';


Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'home',
      component:Home,
      props:true
    },
    {
      path:'/kinds',
      name:'kinds',
      component:Kinds
    },
    {
      path:'/shopping',
      name:'shopping',
      component:Shopping
    },
    {
      path:'/mine',
      name:'mine',
      component:Mine,
      redirect:to => {
        if(!localStorage.isLogin){
          return '/mine/login'
        }else {
          return "/mine/personal"
        }
      },
      children:[
          {
            path:'login',
            title:'login',
            component:Login
          },
          {
            path:'register',
            title:'register',
            component:Register
          },
          {
            path:'personal',
            title:'personal',
            component:Personal
          }
      ]
    },
    {
      name:'details',
      path:'/details',
      component:Details,
      props: (route) => ({ query: route.query.q })
    },
    {
      name:'buybook',
      path:'/buybook',
      component:Buybook,
      props: (route) => ({ query: route.query.q })
    },
    {
      name:'material',
      path:'/material',
      component:Material,
      props: (route) => ({ query: route.query.q })
    },
    {
      name:'adress',
      path:'/adress',
      component:Adress
    },
    {
      name:'payment',
      path:'/payment',
      component:Payment
    }
  ]
});



