<template>
    <div class="payment content">
        <div class="pay-head">
            <router-link to="/kinds"><div class="go-back"><Icon type="ios-arrow-back" /></div></router-link>
            <div class="pay-center"><p>支付中心</p></div>
        </div>
        <div class="pay-content">
            <p>商品信息确认</p>
            <div class="pay-list">
                <div class="list-box" v-for="(item, index) in payCenter" :key="index">
                    <div class="list-left box">
                        <img :src="item.payCover" alt="">
                    </div>
                    <div class="list-right box">
                        <div class="pay-name"><p>{{item.payName}}</p></div>
                        <div class="pay-author"><p>{{item.payAuthor}}</p></div>
                        <div class="pay-allprice"><p>￥{{(item.allPrice).toFixed(2)}}</p></div>
                        <div class="pay-count"><p>x{{item.count}}</p></div>
                    </div>
                </div>
                <div class="pay-adress">
                    <p class="title">收货信息</p>
                    <div class="adress-info">
                        <span class="adress-left">收货地址：</span>
                        <span class="adress-right">四川省成都市武侯区天府三街B5区</span>
                    </div>
                    <div class="adress-per">
                        <span class="adress-left">收货人：</span>
                        <span class="adress-right">yflees</span>
                    </div>
                    <div class="adress-code">
                        <span class="adress-left">邮编：</span>
                        <span class="adress-right">610000</span>
                    </div>
                    <div class="adress-tel">
                        <span class="adress-left">电话：</span>
                        <span class="adress-right">15927223987</span>
                    </div>
                </div>
                <p class="pay-detail">选择支付方式</p>
                <div class="pay-method">
                    <div class="method-list">
                        <span class="icon"><i class="iconfont weico">&#xe60f;</i></span>
                        <span class="text">微信支付</span>
                        <span class="const"><Icon type="ios-arrow-forward" /></span>
                    </div>
                    <div class="method-list">
                        <span class="icon"><i class="iconfont payc">&#xe600;</i></span>
                        <span class="text">支付宝</span>
                        <span class="const"><Icon type="ios-arrow-forward" /></span>
                    </div>
                    <div class="method-list">
                        <span class="icon"><i class="iconfont carp">&#xe779;</i></span>
                        <span class="text">银行卡</span>
                        <span class="const"><Icon type="ios-arrow-forward" /></span>
                    </div>
                </div>
                <div class="countMon">
                    <span class="countNum">共计：</span>
                    <span class="payMoney">￥{{moneyChange}}</span>
                </div>
            </div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        name:'Payment',
        data(){
            return {
                payCenter:{},
                mon:0
            }
        },
        created(){
            let payList = [];
            if(localStorage.payNum){
                payList = JSON.parse(localStorage.payNum);
            }
            localStorage.payNum = JSON.stringify(payList);
            this.payCenter = payList;
        },
        computed:{
            // 总价变化
            moneyChange(){
                let payCent = this.payCenter;
                for(let i = 0,len = payCent.length;i < len;i++){
                    this.mon += parseFloat(payCent[i].allPrice);
                }
                return this.mon.toFixed(2); 
            }
        }
    }
</script>

<style scoped>
    * {
        box-sizing:border-box;
    }
    @font-face {
        font-family: 'iconfont';  /* project id 959545 */
        src: url('//at.alicdn.com/t/font_959545_0r0qijsp3o8h.eot');
        src: url('//at.alicdn.com/t/font_959545_0r0qijsp3o8h.eot?#iefix') format('embedded-opentype'),
        url('//at.alicdn.com/t/font_959545_0r0qijsp3o8h.woff') format('woff'),
        url('//at.alicdn.com/t/font_959545_0r0qijsp3o8h.ttf') format('truetype'),
        url('//at.alicdn.com/t/font_959545_0r0qijsp3o8h.svg#iconfont') format('svg');
    }
    .iconfont{
        font-family:"iconfont" !important;
        font-style:normal;
        -webkit-font-smoothing: antialiased;
        -webkit-text-stroke-width: 0.2px;
        -moz-osx-font-smoothing: grayscale;
    }
    .payment {
        width:100%;
        position:relative;
        margin:0;
        padding:0;
    }
    .pay-head {
        width:100%;
        height:64px;
        color:#fff;
        background:#444;
        position:fixed;
        top:0;
        left:0;
        line-height:64px;
    }
    a .go-back {
        width:25px;
        display:inline-block;
        vertical-align:top;
        color:#fff;
    }
    .ivu-icon-ios-arrow-back {
        font-size:26px;
    }
    .pay-center {
        display:inline-block;
        vertical-align:top;
        font-size:18px;
        width:calc(100% - 30px);
    }
    .pay-center p {
        width:90%;
    }
    .pay-content {
        width:100%;
        padding:0 10px 50px;
        background:#eeeeee;
    }
    .pay-content > p {
        font-size:16px;
        text-align:left;
        height:40px;
        line-height:40px;
    }
    .pay-list {
        width:100%;
        
    }
    .list-box {
        margin:0 auto;
        padding:10px 0;
        width:90%;
        height:140px;
        margin-bottom:10px;
        background:#fff;
    }
    .box {
        display:inline-block;
        vertical-align:top;
    }
    .list-left {
        width:32%;
        height:100%;
    }
    .list-left img {
        width:100%;
        height:100%;
    }
    .list-right {
        margin-left:10px;
        width:60%;
        height:100%;
        text-align:left;
    }
    .pay-name {
        margin-bottom:5px;
        width:100%;
        font-size:14px;
        color:#000;
    }
    .pay-author p {
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    .pay-author {
        font-size:14px;
        color:#555;
        letter-spacing:2px;
        margin-bottom:4px;
    }
    .pay-allprice {
        color:red;
        font-size:16px;
    }
    .pay-count {
        font-size:16px;
        color:#e65122;
        letter-spacing:2px;
    }
    .pay-adress {
        margin:20px auto;
        padding:5px 10px 10px;
        width:90%;
        background:#fff;
        text-align:left;
        box-sizing:border-box;
    }
    .title {
        font-size:16px;
        height:40px;
        line-height:40px;
        color:#000;
    }
    .adress-left {
        font-size:14px;
        color:#666;
        display:inline-block;
        width:27%;
    }
    .adress-right {
        font-size:14px;
        color:#000;
        display:inline-block;
    }
    .adress-info {
        margin:5px 0;
    }
    .adress-per {
        margin:10px 0;
    }
    .adress-code,.adress-tel {
        margin-bottom:10px;
    }
    .pay-detail {
        width:100%;
        height:40px;
        color:#000;
        font-size:16px;
        text-align:left;
        line-height:40px;
    }
    .pay-method {
        margin:10px auto 0;
        width:90%;
        background:#fff;
        box-sizing:border-box;
    }
    .method-list {
        width:100%;
        height:40px;
        border-bottom:1px solid #eeeeee;
    }
    .method-list span {
        display:inline-block;
        vertical-align:middle;
    }
    .icon {
        width:20%;
        font-size:22px;
    }
    .weico {
        color:#6c3;
    }
    .payc {
        color:#ff9933;
    }
    .carp{
        color:#0cf;
    }
    .text {
        width:57%;
        text-align:left;
        font-size:14px;
        line-height:40px;
    }
    .const {
        width:20%;
    }
    .ivu-icon-ios-arrow-forward{
        font-size:18px;
        line-height:0;
    }
    .countMon {
        width:90%;
        margin:20px auto 0;
        padding:0 15px;
        background:#fff;
        font-size:16px;
        text-align:right;
        height:40px;
        line-height:40px;
    }
    .countNum {
        color:#000;
    }
    .payMoney {
        color:red;
        font-weight:bold;
    }
   

</style>


