<template>
    <div class="login">
        <div class="login-box">
            <div class="login-infor">
                <div class="tel">
                    <Icon type="md-phone-portrait" />
                    <input type="number" v-model="usertel" placeholder="请输入您的手机号码">
                </div>
                <div class="pas">
                    <Icon type="md-lock" />
                    <input type="text" v-model="userpas" placeholder="请输入您的密码">
                </div>
                <div class="btn" @click="loginValidate()"><button type="button"></button>登录</div>
            </div>
        </div>
    </div>
</template>


<script>
    export default {
        title:"Login",
        data(){
            return {
                usertel:'',
                userpas:''
            }
        },
        methods:{
            // 判断登录时输入的信息
            loginValidate(){
                let flag = false;
                if((!this.usertel || !this.userpas) || (this.usertel.length != 11 || this.userpas.length != 6)){
                    alert('输入信息为空或格式错误!');
                }else {
                    // 解析本地存储用户信息,与输入信息的对比
                    if(localStorage.userInfo){
                        let user = JSON.parse(localStorage.userInfo);
                        for(let i = 0, len = user.length;i < len;i++){
                            if(user[i].tel == this.usertel && user[i].pass == this.userpas){
                                alert("登录成功!");
                                this.$router.push('/mine/personal');
                                localStorage.isLogin = true; 
                                flag = true;
                                break;
                            }
                        }
                    }
                };
                if(!flag){
                    alert("登录失败，用户不存在");
                }  
            }
        }
    }
</script>


<style scoped>

.login-box {
    margin-top:20px;
    width:100%;
}
.login-infor {
    width:100%;
}
input {
    outline:none;
    border:none;
}
.tel,.pas {
    margin-top:10px;
    height:60px;
    border-bottom:1px solid #ddd;
}
.tel input,
.pas input {
    width:85%;
    height:100%;
    font-size:17px;
    color:#000;
    padding-left:10px;
    vertical-align:middle;
}
.ivu-icon-md-phone-portrait,
.ivu-icon-md-lock {
    font-size:26px;
    color:#888;
}
.btn {
    margin-top:40px;
    height:48px;
    width:100%;
    line-height:48px;
    background:#0c9e0c;
    color:#fff;
    font-size:18px;
    letter-spacing:15px;
    text-align:center;
    border-radius:5px;
}




</style>


