<template>
    <div class="home content">
         <swiper :options="swiperOption" ref="mySwiper">
            <!-- slides -->
            <swiper-slide><img src="https://img20.360buyimg.com/da/jfs/t29020/278/566170799/136308/91350c1e/5bf755c6Nfe335f24.jpg!q80.webp" alt=""></swiper-slide>
            <swiper-slide><img src="https://img10.360buyimg.com/da/jfs/t30172/186/562118305/82279/49517c99/5bf74f39Ne9d6f745.jpg!q95.webp" alt=""></swiper-slide>
            <swiper-slide><img src="https://img10.360buyimg.com/da/jfs/t29425/266/531648999/96153/466c288a/5bf67467N134787c4.jpg!q95.webp" alt=""></swiper-slide>
            <swiper-slide><img src="https://img12.360buyimg.com/da/jfs/t30808/72/488580795/76781/27be68ca/5bf50b0eN019cf85d.jpg!q95.webp" alt=""></swiper-slide>
            <swiper-slide><img src="https://img30.360buyimg.com/da/jfs/t29335/359/789651234/209363/af4cd8c8/5bfe29fcN97f3b031.jpg!q95.webp" alt=""></swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>
            <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
        <div class="inform">
            <Icon type="md-notifications-outline" />
            <p class="infoma">双十二惊爆价，好书好看好价钱!</P>
        </div>
        <div class="data-box">
            <p class='newBook'>新书上架</p>
            <div class="books-data" v-for="(booksInfo,index) in booksInfo" :key="index">
                <router-link :to="{name:'details',query:{id:booksInfo.id}}" @click="getData(booksInfo,index)">
                    <div class="data-img" ><img :src="booksInfo.cover" alt=""></div>
                </router-link>
                <p class="data-title">{{booksInfo.name}}</p>
                <div class="data-price">￥ {{booksInfo.price}}</div>
            </div>
        </div>
        <div class="data-box">
            <p class='newBook'>编辑推荐</p>
            <div class="books-data" v-for="(booksInfo,index) in booksInfo" :key="index">
                <router-link :to="{name:'details',query:{id:booksInfo.id}}" @click="getData(booksInfo,index)">    
                    <div class="data-img"><img :src="booksInfo.cover" alt=""></div>
                </router-link>
                <p class="data-title">{{booksInfo.name}}</p>
                <div class="data-price">￥ {{booksInfo.price}}</div>
            </div>
        </div>
        <div class="data-box">
            <p class='newBook'>热门新书</p>
            <div class="books-data" v-for="(booksInfo,index) in booksInfo" :key="index">
                <router-link :to="{name:'details',query:{id:booksInfo.id}}" @click="getData(booksInfo,index)">    
                    <div class="data-img"><img :src="booksInfo.cover" alt=""></div>
                </router-link>
                <p class="data-title">{{booksInfo.name}}</p>
                <div class="data-price">￥ {{booksInfo.price}}</div>
            </div>
        </div>
        <router-view></router-view>
       
    </div>
</template>

<script>

import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
        name:'Home',
        pic: 'Swiper',
        data(){
            return {
                swiperOption: {
                    autoplay:true,
                    delay:1000,
                    loop: true,
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    }
                },
                booksInfo:[],
                id:''
            }
        },
        components: {
            swiper,
            swiperSlide
        },
        created() {
            let url = "../static/json/home-books.json";
            this.$axios.get(url, {
                params:{

                }
            }).then(res => {
                let booksInfo = res;
                this.booksInfo = res.data;
                });
        },
        computed: {
           swiper() {
                return this.$refs.mySwiper.swiper
            }
        },
        methods:{
            getData(booksInfo,index){
                this.$router.push("/details",this.booksInfo[index]);
            }
        }
    }
</script>

<style scoped>
img {
    width:100%;
}
.inform {
    width:100%;
    border-bottom:1px solid #ccc;
}
.ivu-icon-md-notifications-outline {
    font-size:26px;
    color:orange;
    vertical-align:middle;
}
.infoma {
    padding:7px 0;
    color:#000;
    font-size:17px;
    display:inline-block;
    vertical-align:middle;
}
.data-box {
    margin:20px 0 40px;
    width:100%;
}
.newBook {
    margin-left:15px;
    font-size:18px;
    font-weight:bold;
    color:#000;
    text-align:left;
}
.books-data {
    margin-top:15px;
    width:43%;
    display:inline-block;
    vertical-align:top;
    text-align:left;
}
.books-data:nth-child(2n){
    margin-right:17px;
}
.books-data .data-img {
    width:90%;
    height:183px;
}
.data-img img {
    width:100%;
    height:100%;
}
.data-title {
    margin-top:11px;
    font-size:17px;
    color:#000;
    text-overflow:ellipsis;
    overflow:hidden;
    white-space:nowrap;
}
.data-price {
    color:red;
    font-size:16px;
}


</style>


