<template>
    <div class="material content">
        <div class="material-box">
            <div class="header">
                <router-link to="/mine/personal">
                    <div class="goback"><Icon type="ios-arrow-back" /></div>
                </router-link>
                <div class="center">个人信息中心</div>
            </div>
            <div class="box-input">
                <input type="text" placeholder="昵称"></input>
                <span>yflees</span>
            </div>
            <div class="box-input">
                <input type="text" placeholder="E-mail"></input>
                <span>请填写您的安全邮箱</span>
            </div>
            <div class="box-input">
                <input type="text" placeholder="性别"></input>
                <form class="gender" name="textForm" action="">
                    <div class="man">
                        <label name='gender'>男</label>
                        <input type="radio" name='gender'></input>
                    </div>
                    <div class="woman">
                        <label name='gender'>女</label>
                        <input type="radio" name='gender'></input>
                    </div>
                    <div class="pas">
                        <label name='gender'>保密</label>
                        <input type="radio" name='gender' checked="checked"></input>
                    </div>
                </form>
            </div>
            <div class="box-input">
                <input type="text" placeholder="生日"></input>
                <select class="birth"><option >{{birthChange}}</option></select>
            </div>
            <div class="box-input">
                <input type="text" placeholder="身份证号"></input>
                <span>请填写身份证号码</span>
            </div>
            <div class="input-des">
                <input type="text" placeholder="个人描述"></input>
                <span>谈谈您对自己的认识</span>
            </div>
            <div class="material-btn">确认修改</div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        name:'Material',
        data(){
            return {
                birthday:''
            }
        },
        computed:{
            birthChange(){
                let date = new Date();
                return this.birthday = date.toDateString();
            }
        }
    }
</script>

<style scoped>
    .material {
        width:100%;
        background:rgba(243,243,243);
        box-sizing:border-box;
    }
    .header {
        width:100%;
        height:64px;
        background:#444;
        color:#fff;
        position:fixed;
        top:0;
        left:0;
    }
    .center,.goback {
        display:inline-block;
        line-height:64px;
        font-size:18px;
    }
    .ivu-icon-ios-arrow-back {
        font-size:28px;
        color:#fff;
    }
    .center {
        width:calc(100% - 50px);
    }
    .goback {
        width:40px;
    }
    .box-input {
        width:100%;
        height:50px;
        border-bottom:1px solid #ccc;
        background:#fff;
        position:relative;
    }
    .box-input > input {
        padding-left:10px;
        width:99%;
        height:100%;
        border:none;
        font-size:14px;
        outline:none;
    }
    .gender {
        position:absolute;
        left:90px;
        top:50%;
        transform:translateY(-50%);
    }
    .man,.woman,.pas {
        margin-right:5px;
        display:inline-block;
        vertical-align:top;
    }
    .gender label {
        font-size:14px;
        color:#000;
        vertical-align:middle;
    }
    .gender input {
        width:16px;
        height:16px;
        vertical-align:middle;
    }
    .box-input > span {
        display:inline-block;
        text-align:left;
        font-size:14px;
        color:#000;
        position:absolute;
        left:90px;
        top:50%;
        transform:translateY(-50%);
    }
    .birth {
        width:120px;
        height:36px;
        border:1px solid #ccc;
        line-height:36px;
        position:absolute;
        left:90px;
        top:50%;
        transform:translateY(-50%);
    }
    .input-des {
        width:100%;
        height:120px;
        background:#fff;
    }
    .input-des > input {
        padding-left:10px;
        text-align:left;
        width:40%;
        height:100%;
        font-size:14px;
        outline:none;
        border:none;
    }
    .input-des > span {
        width:59%;
        display:inline-block;
        font-size:14px;
        color:#000;
    }
    .material-btn {
        margin:30px auto 0;
        width:90%;
        background:#279a27;
        height:50px;
        color:#fff;
        font-size:18px;
        letter-spacing:3px;
        line-height:50px;
        border-radius:5px;
    }
</style>


