<template>
    <div class="buy content">
        <div class="buy-head">
            <router-link to="/kinds"><Icon type="ios-arrow-back" /></router-link>
            <div class="buy-txt">详情页</div>
        </div>
        <div class="buy-box">
            <div class="buy-book">
                <div class="book-left deta"><img :src="kindsData.cover" alt=""></div>
                <div class="book-right deta">
                    <div class="book-info">
                        <span>作者</span>
                        <p class="buy-author">{{kindsData.author}}</p>
                    </div>
                    <div class="book-info">
                        <span>页数</span>
                        <p>{{kindsData.page}}</p>
                    </div>
                    <div class="book-info">
                        <span>书号</span>
                        <p>{{kindsData.bookNum}}</p>
                    </div>
                    <div class="book-info">
                        <span>出版日期</span>
                        <p>{{kindsData.date}}</p>
                    </div>
                    <div class="book-info">
                        <span>售价</span>
                        <p class="buy-price">￥{{kindsData.price}}</p>
                    </div>
                </div>
            </div>
            <div class='buy-name'><h3>{{kindsData.name}}</h3></div>
            <div class="buy-cart">
                <div class="add-cart cart" @click="buyBook()" v-show="box">加入购物车</div>
                <router-link to="/payment" class="buy-pay cart"><div @click="payCenter()">立即购买</div></router-link>
            </div>
            <fieldset class="buy-data">
                <legend>内容简述</legend>
                <div class="buy-des">{{kindsData.describe}}</div>
            </fieldset>
            <fieldset class="buy-data">
                <legend>内容简述</legend>
                <div class="buy-des">{{kindsData.info1}}</div>
            </fieldset>
            <fieldset class="buy-data">
                <legend>作者简介</legend>
                <div class="buy-des">{{kindsData.authorInfo}}</div>
            </fieldset>
            <div class="mask" v-show="ok">
                <div class="mask-con">
                    <div class="con-top">{{alertText}}</div>
                    <div class="con-bottom">
                        <span @click="closeBtn">确认</span>
                    </div>
                </div>
            </div>
        </div>

        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        name:'Buybook',
        data(){
            return {
                kindsData:{},
                id:'',
                alertText:"添加成功！",
                ok:false,
                box:true
            }
        },
        created() {
            // 请求本地数据
            let id = this.$route.query.id;
            let url = "../../static/json/book-info.json";
            this.$axios.get(url,{
                params:{}
            }).then(res=> {
                this.kindsData = res.data[id];
            });
        },
        methods:{
            // 弹框的关闭
            closeBtn(){
                this.ok = false;
            },
            // 点击加入购物车
            buyBook(){
                if(!localStorage.isLogin){
                    alert("请登录!");
                    return;
                }
                let bookData = {
                    name:this.kindsData.name,
                    author:this.kindsData.author,
                    price:this.kindsData.price,
                    cover:this.kindsData.cover,
                    bookNum:this.kindsData.bookNum,
                    num:1,
                    total:this.kindsData.price,
                };
                let bookArr = [];
                // 判断本地是否已有订单数据，如果有则等于
                if(localStorage.bookData){
                    bookArr = JSON.parse(localStorage.bookData);
                 };
                let flag = false;
                // 处理本地存储的重复数据
                for(let i = 0,len = bookArr.length;i < len;i++){
                    if(bookArr[i].bookNum == bookData.bookNum){
                        bookArr[i].num = bookArr[i].num + 1;
                        bookArr[i].total = (bookArr[i].num * bookArr[i].price);
                        flag = true;
                        break;
                    }
                };
                // 如果本地数据没有则添加
                if(!flag){
                    bookArr.push(bookData);
                }
                this.ok = true;
                localStorage.bookData = JSON.stringify(bookArr);
            },
            // 点击立即购买
            payCenter(){
                let payNum = {
                    payName:this.kindsData.name,
                    payAuthor:this.kindsData.author,
                    payCover:this.kindsData.cover,
                    payPrice:this.kindsData.price,
                    payNumber:this.kindsData.bookNum,
                    count:1,
                    allPrice:this.kindsData.price
                }
                let payArr = [];
                if(localStorage.payNum){
                    payArr = JSON.parse(localStorage.payNum);
                }
                let isPay = false;
                // 处理重复数据
                for(let i = 0,len = payArr.length;i < len;i++){
                    if(payArr[i].payNumber == payNum.payNumber){
                        payArr[i].count = payArr[i].count + 1;
                        payArr[i].allPrice = (payArr[i].payPrice * payArr[i].count);
                        isPay = true;
                        break;
                    }
                }
                if(!isPay){
                    payArr.push(payNum);
                }
                localStorage.payNum = JSON.stringify(payArr);
            }
        }
    }
</script>
    


<style scoped>
    .buy {
        width:100%;
        margin:0;
        padding:0;
        box-sizing:border-box;
    }
    .buy-box {
        width:100%;
        padding:10px 15px;
    }
    /**头部 */
    .buy-head {
        width:100%;
        height:64px;
        background:#444;
        line-height:64px;
        position:fixed;
        top:0;
        left:0;
    }
    .buy-txt {
        width:88%;
        color:#fff;
        font-size:18px;
        display:inline-block;
        padding-right:32px;
    }
    .ivu-icon-ios-arrow-back {
        width:10%;
        color:#fff;
        font-size:28px;
        display:inline-block;
    }
    .buy-book {
        width:100%;
        height:155px;
        margin-bottom:8px;
    }
    .deta {
        display:inline-block;
        vertical-align:top;
        height:100%;
    }
    .book-left {
        width:33%;
        margin-right:11px;
    }
    .book-left img {
        width:100%;
        height:100%;
    }
    .book-right {
        width:62%;
    }
    .book-info {
        width:100%;
        height:30px;
        text-align:left;
        font-size:14px;
        line-height:30px;
    }
    .buy-author {
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
        vertical-align:top;
    }
    .book-info .buy-price {
        font-size:18px;
        color:red;
        vertical-align:middle;
    }
    .book-info span {
        width:32%;
        display:inline-block;
        color:#888;
    }
    .book-info p {
        display:inline-block;
        width:65%;
        color:#000;
    }
    .buy-name {
        margin:10px 0;
        height:35px;
        font-size:16px;
        color:#000;
        text-align:left;
        line-height:35px;
    }
    .buy-name h3 {
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    .buy-box .buy-cart {
        margin:10px 0 15px;
        width:100%;
        height:50px;
        display:flex;
        justify-content:space-around;
        align-items:center;
    }
    .cart {
        width:45%;
        display:inline-block;
        line-height:50px;
        font-size:18px;
        color:#fff;
    }
    .add-cart {
        border:1px solid #198c19;
        background:#198c19;
        border-radius:5px;
    }
    .buy-pay {
        border:1px solid #e72727;
        background:#e72727;
        border-radius:5px;
    }
    .buy-data {
        margin-top:30px;
        padding:15px;
        font-size:16px;
        text-align:left;
    }
    .buy-data legend {
        font-size:20px;
        color:#000;
        font-weight:bold;
    }
    .buy-des {
        color:#333;
    }
    /*弹框*/ 
    .mask {
        width:100%;
        height:100%;
        background:rgba(0,0,0,0.3);
        position:fixed;
        top:0;
        left:0;
    }
    .mask-con {
        width:240px;
        height:130px;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        background:#fff;
        border-radius:8px;
    }
    .mask-con .con-top {
        width:100%;
        height:60%;
        text-align:center;
        line-height:78px;   
        font-size:20px;
        color:black;
        letter-spacing:2px;
    }
    .mask-con .con-bottom {
        width:100%;
        height:calc(100% - 60%);
        color:#fff;
        font-size:0;
        box-sizing:border-box;
    }
    .mask-con .con-bottom span {
        box-sizing:border-box;
        width:100%;
        line-height:52px;
        text-align:center;
        display:inline-block;
        background:cornflowerblue;
        font-size:16px;  
        cursor:pointer;
    }
    .mask-con .con-bottom span{
       border-bottom-left-radius:8px;
       border-bottom-right-radius:8px;
    }

</style>


