import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        text:'首页',
        isLogin:false
    },
    mutations:{
        headChange(state,text){
            state.text = text;
        },
        changeState(state,isLogin){
            state.isLogin = isLogin;
        }
    },
    getters: {
        msg(state) {
            return state.isLogin ? "对不起，您未登录" : "欢迎您";
        }
    }
})


