<template>
    <div class="kinds content">
        <div class="search">
            <Icon @click="searchBtn()" type="ios-search-outline" />
            <input type="search" v-model="keywords" placeholder="请输入您想查找的图书名称进行搜索"></input>
        </div>
        <div class="kinds-box">
            <div class="kinds-list" v-for="(item, index) in booksAll" :key="index">
                <router-link :to="{name:'buybook',query:{id:item.id}}" @click="getDa(booksAll,index)">
                    <div class="kinds-left">
                        <div class="left-img"><img :src="item.cover" alt=""></div>
                        <div class="left-des">
                            <p class="left-instru">{{item.name}}</p>
                            <p class="left-author">{{item.author}}</p>
                            <span class="left-price">￥ {{item.price}}</span>
                        </div>
                    </div>
                    <div class="kinds-right">
                        <div>
                            <Icon type="ios-arrow-forward" />
                        </div>
                    </div>
                </router-link>
            </div>
        </div>
        <p>加载更多...</p>

        <router-view></router-view>


    </div>
</template>

<script>
    export default {
        name:'Kinds',
        data(){
            return {
                booksAll:'',
                id:'',
                keywords:'',
                bookSea:''
            }
        },
        created() {
            let url = "../static/json/book-info.json";
            this.$axios.get(url, {
                params:{

                }
            }).then(res => {
                this.booksAll = res.data;
                this.bookSea = res.data;
                });
        },
        methods:{
            getDa(booksAll,index){
                this.$router.push('/buybook',this.booksAll[index]);
            },
            // 搜索
            searchBtn(){
                let serachArr = [];
                for(let i = 0;i < this.booksAll.length;i++){
                    let reg = new RegExp(this.keywords,"i");
                    if((reg.test(this.booksAll[i].name) || reg.test(this.booksAll[i].price)) && (this.keywords != '')){
                        serachArr.push(this.booksAll[i]);
                    }
                }
            }
        }
    }
</script>

<style scoped>

.kinds {
    padding:0 12px;
    box-sizing:border-box;
}
.kinds > p {
    margin:15px 0;
    color:black;
}
.search {
    width:100%;
    margin-top:10px;
    height:44px;
    position:relative;
}
.ivu-icon-ios-search-outline {
    font-size:24px;
    color:#ccc;
    position:absolute;
    left:9px;
    top:12px;
}
input {
    outline:none;
    border:none;
}
.search > input {
    padding-left:33px;
    width:100%;
    height:100%;
    font-size:16px;
    border:1px solid #aaa;
    border-radius:50px;
    line-height:45px;
} 
.kinds-box {
    margin-top:20px;
} 
.kinds-list {
    width:100%;
    height:151px;
    padding:10px 0;
    border-bottom:1px solid #ccc;
}
.kinds-list .kinds-left {
    width:86%;
    text-align:left;
    display:inline-block;
    vertical-align:top;
}
.kinds-list .kinds-right {
    width:calc(100% - 87.2%);
    display:inline-block;
    vertical-align:top;
}
.ivu-icon-ios-arrow-forward {
    font-size:22px;
    color:#ccc;
    line-height:131px;
}
.kinds-left .left-img {
    width:30%;
    height:118px;
    display:inline-block;
    vertical-align:top;
}
.kinds-left .left-img img {
    width:100%;
    height:100%;
}
.kinds-left .left-des {
    margin-left:8px;
    padding:12px 0;
    width:calc(100% - 35%);
    display:inline-block;
    vertical-align:top;
}
.kinds-left .left-instru {
    margin-bottom:12px;
    font-size:16px;
    color:blue;
    text-overflow:ellipsis;
    overflow:hidden;
    white-space:nowrap;
}
.kinds-left .left-author {
    margin-bottom:14px;
    letter-spacing:1px;
    font-size:14px;
    color:#666;
    text-overflow:ellipsis;
    overflow:hidden;
    white-space:nowrap;
}
.kinds-left .left-price {
    font-size:20px;
    color:red;
}

</style>


