<template>
    <div class="adress content">
        <div class="adress-head">
            <router-link to="/mine/personal">
                <div class="go-back"><Icon type="ios-arrow-back" /></div>
            </router-link>
            <div class="daress-center"><p>收货管理</p></div>
        </div>
        <div>
            <div class="adress-list">
                <div class="list">
                    <input type="text" placeholder="姓名">
                    <span>请填写您的真实姓名</span>
                </div>
                <div class="list">
                    <input type="text" placeholder="联系电话">
                    <span>请填写您的联系电话</span>
                </div>
                <div class="list">
                    <input type="text" placeholder="邮政编码">
                    <span>请填写您的邮政编码</span>
                </div>
                <div class="adress-inp">
                    <select>
                        <option>四川省</option>
                        <option>湖北省</option>
                        <option>广东省</option>
                        <option>浙江省</option>
                        <option>江西省</option>
                    </select>
                    <select>
                        <option>成都市</option>
                        <option>武汉市</option>
                        <option>广州市</option>
                        <option>杭州市</option>
                        <option>南昌市</option>
                    </select>
                    <select>
                        <option>武侯区</option>
                        <option>江岸区</option>
                        <option>白云区</option>
                        <option>萧山区</option>
                        <option>青山湖区</option>
                    </select>
                </div>
                <div class="list">
                    <input type="text" placeholder="详细地址">
                    <span>请填写详细的街道、楼栋、门牌号</span>
                </div>
            </div>
            <div class="adress-btn">确认地址</div>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        name:'Adress',
        data(){
            return {

            }
        }
    }
</script>

<style scoped>
    .adress {
        width:100%;
        background:#eeeeee;
    }
    .adress-head {
        width:100%;
        height:64px;
        background:#444;
        position:fixed;
        top:0;
        left:0;
    }
    .go-back,.daress-center {
        display:inline-block;
        color:#fff;
        line-height:64px;
        vertical-align:top;
    }
    .daress-center {
        width:calc(100% - 45px);
        height:64px;
        font-size:18px;
        position:relative;
    }
    .daress-center p {
        display:inline-block;
        position:absolute;
        top:50%;
        left:36%;
        transform:translateY(-50%);
    }
    .go-back {
        width:40px;
    }
    .ivu-icon-ios-arrow-back {
        font-size:28px;
        color:#fff;
    }
    .adress-list {
        width:100%;
        background:#fff;
    }
    .list {
        width:100%;
        height:50px;
        border-bottom:1px solid #ccc;
        position:relative;
        font-size:14px;
    }
    .list input {
        width:95%;
        height:100%;
        border:none;
        outline:none;
    }
    .list span {
        position:absolute;
        left:95px;
        top:50%;
        transform:translateY(-50%);
        color:#000;
    }
    .adress-inp {
        width:100%;
        height:50px;
        line-height:50px;
        border-bottom:1px solid #ccc;
    }
    .adress-inp select {
        width:25%;
        height:34px;
        border:1px solid #000;
        outline:none;
        margin-right:8px;
    }
    .adress-inp select option {
        border:1px solid #000;
        outline:none;
    }
    .adress-btn {
        margin:30px auto 0;
        width:90%;
        height:50px;
        line-height:50px;
        background:#279a27;
        color:#fff;
        font-size:18px;
        border-radius:5px;
        letter-spacing:2px;
    }
    
</style>


