<template>
    <div class="details content">
        <div class="detail-head">
            <router-link to="/"><Icon type="ios-arrow-back" /></router-link>
            <div class="head-txt">书籍简介</div>
        </div>
        <div class="details-box">
            <div class="details-book">
                <div class="book-left deta"><img :src="books.cover" alt=""></div>
                <div class="book-right deta">
                    <div class="book-info">
                        <span>作者</span>
                        <p>{{books.author}}</p>
                    </div>
                    <div class="book-info">
                        <span>页数</span>
                        <p>{{books.page}}</p>
                    </div>
                    <div class="book-info">
                        <span>书号</span>
                        <p>{{books.bookNum}}</p>
                    </div>
                    <div class="book-info">
                        <span>出版日期</span>
                        <p>{{books.date}}</p>
                    </div>
                   <div class="book-info1">
                        <span>详情</span>
                        <Icon type="ios-arrow-forward" />
                    </div>
                </div>
            </div>
            <div class="details-title"><h3>{{books.name}}</h3></div>
            <div class="details-des">{{books.describe}}</div>
            <div class="details-price">
                <p class="price">￥{{books.price}}</p>
                <p class="num">1本</p>
            </div>
        </div>
    </div>
</template>

<script>

    export default {
        name:'Details',
        data(){
            return {
                books:{},
                id:''
            }
        },
        created(){
            let id = this.$route.query.id;
            let url = "../../static/json/home-books.json";
            this.$axios.get(url, {
                params:{
                   
                }
            }).then(res => {
                this.books = res.data[id];
                });
        }
    }
</script>

<style scoped>
    .details {
        width:100%;
    }
    .details-box {
        width:100%;
        padding:10px 15px;
    }
    .detail-head {
        width:100%;
        height:64px;
        background:#444;
        line-height:64px;
        position:fixed;
        top:0;
        left:0;
    }
    .head-txt {
        color:#fff;
        display:inline-block;
        width:88%;
        font-size:18px;
        padding-right:25px;
    }
    .ivu-icon-ios-arrow-back {
        width:10%;
        color:#fff;
        display:inline-block;
        font-size:28px;
    }
    .details-book {
        width:100%;
        height:150px;
        margin-bottom:8px;
    }
    .deta {
        display:inline-block;
        vertical-align:top;
        height:100%;
    }
    .book-left {
        width:33%;
        margin-right:11px;
    }
    .book-left img {
        width:100%;
        height:100%;
    }
    .book-right {
        width:62%;
    }
    .book-info {
        width:100%;
        height:30px;
        text-align:left;
        font-size:14px;
        line-height:30px;
    }
    .book-info span {
        width:32%;
        display:inline-block;
        color:#888;
    }
    .book-info p {
        display:inline-block;
        vertical-align:middle;
        color:#000;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    .book-info1 {
        height:30px;
        text-align:right;
        color:#888;
        font-size:14px;
    }
    .ivu-icon-ios-arrow-forward {
        display:inline-block;
        line-height:22px;
    }
    .details-title {
        margin-bottom:10px;
        width:100%;
        height:40px;
        text-align:left;
        line-height:40px;
    }
    .details-title h3 {
        font-size:18px;
        color:#000;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    .details-des {
        width:100%;
        text-align:left;
        font-size:14px;
        color:#777;
    }
    .details-price {
        margin:40px 0 20px;
        width:100%;
        height:45px;
        border:1px solid #333;
        line-height:45px;
    }
    .details-price p {
        width:48.5%;
        display:inline-block;
        font-size:18px;
        font-weight:bold;
    }
    .num {
        color:#000;
    }
    .price {
        border-right:1px solid #333;
        color:red;
    }
    
</style>


