<template>
    <div class="personal">
        <div class="person-head">个人中心</div>
        <div class="person-img">
            <div class="my-img">
                <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1543811996915&di=e9812e01ca1ef7d1695e7f144dd7ae17&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2F0%2F57b27299cbf41.jpg" alt="">
                <p class="per-name" v-for="(item, index) in users" :key="index">{{item.name}}</p>
            </div>
        </div>
        <div class="person-list">
            <router-link to="/material">
                <div class="person-info">
                    <p>设置个人资料</p>
                    <Icon type="ios-arrow-forward" />
                </div>
            </router-link>
            <router-link to="/adress">
                <div class="person-info">
                    <p>收货地址管理</p>
                    <Icon type="ios-arrow-forward" />
                </div>
            </router-link>
            <div class="person-info">
                <p>我的订单</p>
                <Icon type="ios-arrow-forward" />
            </div>
            <div class="person-info">
                <p>我的评价</p>
                <Icon type="ios-arrow-forward" />
            </div>
            <div class="person-info">
                <p>商品收藏</p>
                <Icon type="ios-arrow-forward" />
            </div>
            <div class="person-info">
                <p>店铺收藏</p>
                <Icon type="ios-arrow-forward" />
            </div>
            <div class="person-info">
                <p>浏览记录</p>
                <Icon type="ios-arrow-forward" />
            </div>
        </div>
        <div class="person-btn" @click="outBtn()"><p>退出登录</p></div>

        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        title:'Personal',
        data(){
            return {
                users:{}
            }
        },
        created(){
            // 将用户名解析，显示在页面
            let userArr = [];
            if(localStorage.userInfo){
                userArr = JSON.parse(localStorage.userInfo);
            }
            localStorage.userInfo = JSON.stringify(userArr);
            this.users = userArr;
        },
        methods:{
            outBtn(){
               let out = JSON.parse(localStorage.isLogin);
               out = false;
               localStorage.isLogin = JSON.stringify(out);
               this.$router.push('/mine/login');
            }
        }
    
    }
</script>

<style scoped>
    .personal {
        margin:0;
        padding:0;
        width:100%;
        box-sizing:border-box;
        position:absolute;
        top:64px;
        left:0;
        overflow-y:auto;
        height:calc(100% - 64px - 52px);
    }
    .person-head {
        width:100%;
        height:64px;
        font-size:18px;
        color:#fff;
        line-height:64px;
        background:#444;
        position:fixed;
        top:0;
        left:0;
    }
    .person-img {
        width:100%;
        height:180px;
        background:url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1543811996915&di=e9812e01ca1ef7d1695e7f144dd7ae17&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2F0%2F57b27299cbf41.jpg") no-repeat center center / cover;
    }
    .my-img {
        width:100%;
        height:100%;
        background:rgba(225,225,225,0.7);
        padding-top:25px;
    }
    .my-img img {
        width:100px;
        height:100px;
        border-radius:50%;
        text-align:center;
    }
    .my-img .per-name {
        margin-top:10px;
        font-size:16px;
        color:#ce3636;
        letter-spacing:2px;
    }
    .person-list {
        width:100%;
        padding-top:20px;
        background:#f4f4f4;
        border-bottom:1px solid #ccc;
    }
    .person-info {
        padding:0 10px;
        width:100%;
        height:50px;
        background:#fff;
        border-top:1px solid #dddddd;
        display:flex;
        justify-content:space-between;
    }
    .person-info:nth-child(3),
    .person-info:nth-child(5){
        margin-top:10px;
    }
    .person-info:nth-child(2),
    .person-info:nth-child(4){
        border-bottom:1px solid #dddddd;
    }
    .person-info p {
        font-size:16px;
        color:#333;
        display:inline-block;
        line-height:50px;
    }
    .person-info > .ivu-icon-ios-arrow-forward {
        font-size:20px;
        line-height:50px;
        color:#333;
    }
    .person-btn {
        width:100%;
        padding:30px 0 40px;
        line-height:50px;
        background:#f4f4f4;
    }
    .person-btn > p {
        margin:0 auto;
        width:95%;
        height:50px;
        border-radius:5px;
        background:#bb1c1c;
        font-size:16px;
        color:#fff;
        letter-spacing:2px;
    }
</style>


