// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import Axios from 'axios'
import $ from 'jquery'
// 导入iview
import iView from 'iview';
import 'iview/dist/styles/iview.css';

// use iview
Vue.use(iView);

// 导入
Vue.prototype.$axios = Axios
// 挂载
Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  switch(to.name){
    case 'home' : store.commit('headChange','首页') ;break;
    case 'kinds' : store.commit('headChange','分类') ;break;
    case 'shopping' : store.commit('headChange','购物车') ;break;
    case 'mine' : store.commit('headChange','我的') ;break;
    case 'login' : store.commit('headChange','登录/注册') ;break;
  };
  next();
})




/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



